export default {
	data() {
		return {
			latitude: 29.64356722667575,
			longitude: 106.57172588823506,
			setH:100,
			reportTop:0,
			covers: []
		}
	},
	created(){
		var getH=uni.getSystemInfoSync().windowHeight
		var getW=uni.getSystemInfoSync().windowWidth
		this.setH=getH-getW/750*350;
		this.reportTop=getH-getW/750*513-10
		var that=this
		uni.getLocation({
			type:"gcj02",
			success(res){
				that.longitude=res.longitude
				that.latitude=res.latitude
				console.log(res.longitude)
				console.log(res.latitude)
			}
		})
	},
	onReady(){
		var mapContext=uni.createMapContext("patrolMap")
		mapContext.getCenterLocation({
			success(res){
				console.log(res.longitude)
			}
		})
		this.covers=[{
				id:"person1",
				latitude: 29.64756722667575,
				longitude: 106.57172588823506,
				iconPath: '../../static/images/tag_patrol.png',
				width:10,
				height:10,
				label:{
					content:"黄博泽",
					color:"#5087FB"
				}
			}, {
				id:"person2",
				latitude: 29.64556722667575,
				longitude: 106.57472588823506,
				iconPath: '../../static/images/tag_patrol.png',
				width:10,
				height:10,
				label:{
					content:"歆歆哟"
				}
			}]
	},
	methods: {

	}
}
